import React from "react";
import { screen, render, fireEvent } from "@testing-library/react";
import PaymentScreen from "./../../PaymentScreen";
import "@testing-library/jest-dom/extend-expect";
import { Provider } from "react-redux";
import { configureStore } from "@reduxjs/toolkit";
import { defineFeature, loadFeature } from "jest-cucumber";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  MemoryRouter,
  useLocation,
} from "react-router-dom";
import parkingSlice from "../../redux/reducer/parkingSlice";
let store: any;
const feature = loadFeature("src/components/features/paymentscreen.feature");
defineFeature(feature, (test) => {
  test("User pays for occupied parking spot", ({ given, then, when, and }) => {
    jest.mock("react-router-dom", () => ({
      ...(jest.requireActual("react-router-dom") as {}),
      useLocation: jest.fn().mockImplementation(() => {
        return { pathname: "/payment" };
      }),
    }));
    given("I am on the Payment Screen", () => {
      store = configureStore({
        reducer: {
          parkingSpaces: parkingSlice,
        },
      });
      const state = {
        spaceDetails: {
          registration: "ABC123",
          parkingTime: "08:30",
        },
        space: 1,
      };

      render(
        <Provider store={store}>
          <MemoryRouter initialEntries={["/payment"]}>
            <Routes>
              <Route path="/payment" element={<PaymentScreen />} />
            </Routes>
          </MemoryRouter>
        </Provider>
      );
    });

    and(
      "I see the vehicle registration and hours parked for the occupied parking spot",
      () => {
        const state = {
          spaceDetails: {
            registration: "ABC123",
            parkingTime: "08:30",
          },
          space: 1,
        };
        store = configureStore({
          reducer: {
            parkingSpaces: parkingSlice,
          },
        });
        render(
          <Provider store={store}>
            <MemoryRouter initialEntries={["/payment"]}>
              <Routes>
                <Route path="/payment" element={<PaymentScreen />} />
              </Routes>
            </MemoryRouter>
          </Provider>
        );
      }
    );
  });
});
